@import 'colors';

$blueGray: #464d61;
$blackGray: #101010;
$discoverGray: #040512;
$deepPurple: #090228;
$discoverOrange: rgba(255,68,0,.9);

.discogs-container {
    clear: both;
    margin-bottom: 10px;
    background-color: $deepPurple;
    color: white;
    margin-top: 30px;
    padding: 5px;

}

.floating-thumbnail {
    float: left;
}

.discogs-result-title {
    margin-bottom: auto;
    font-size: 10pt;
    line-height: 100% !important;
}

.youtube-result, .discogs-result {
    border-style: solid;
    border-width: thin;
    background-color: black;
    border-color: #680000;
    padding: 5px;
    display: flex;
}

.discogs-result.record {
    cursor: pointer;
}

.discogs-result-url {
    margin-left: auto;
    float: right;
    color: #fd8eff;
    text-decoration: underline;
    cursor: pointer;
}

.discover-subheader {
    clear: both;
}

.sync-ethereum-button {
    float: left;
    border-style: solid;
    border-width: thin;
    margin-left: 5px;
    padding: 5px;
}

.action-buttons {
    margin-top: 8px;
}

.youtube-result.discogs-result {
    height: 100% !important;
}

.youtube-sampler {
    padding: 10px;
    margin-bottom: 10px;
    background-color: $discoverGray;
    border: solid 1px $blueGray;
    width: 500px;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

    .record-button {
        border-width: 1px !important;
    }

    .record-button.recording {
        background-color: red;
    }
}

.description-pane {
    color: white;
    padding: 10px;
    margin-bottom: 10px;
    background-color: $discoverGray;
    border: solid 1px $blueGray;
    flex: 1;
    margin-left: 35px;
    .pagination-pane {
        .field-pagination-button {
            margin-top: 5px;
            background-color: #464d61 !important;
            color: #bababa;
            border-color: black;
        }
    }
}

.youtube-search-container {
}

.youtube-result-thumbnail {
    width: 50px;
    height: 40px;
    float: left;
}

.youtube-result {
    clear: both;
    margin-top: 5px;
    cursor: pointer;
}

.youtube-result-title, .discogs-result-title {
    float: left;
    font-size: 10pt;
    margin-left: 5px;
    padding-left: 5px;
    height: 40px;
    border-left-style: solid;
    border-left-width: 1px;
    line-height: 35px;
}


.youtube-widget-container {
    width: 400px;
    float: left;
}

.youtube-pane {
    clear: both;
    float: left;
    width: 400px;
}

.sampled-sounds-container {
    clear: both;
    padding: 10px;
    color: white;
    background-color: $discoverGray;
    border: solid 1px $blueGray;
    margin-bottom: 10px;
    float: left;
    width: 500px;
}

.sampled-sounds-header {
    font-size: 10pt;
    margin-bottom: 5px;
}

.sample-button.sampling {
    background-color: red;
    color: white;
}

.video-info-container {
    float: left;
}

.video-buttons-container {
    clear:both;
    padding-bottom: 20px;
}

.sample-source {
    float: left;
}


.discover-toolbar {
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 10px;
    padding-bottom: 2px;
    background-color: $discoverGray;
    border: solid 1px $blueGray;
    color: white;
    margin-bottom: 10px;
    float: left;
    width: 500px;
}

.discogs-header {
    display: flex;
}

.youtube-discogs-container {
    display: flex;
    width: 100%;
}

.youtube-thumbnail {
    width: 60px;
    height: 60px;
    margin-left: 10px;
    cursor: pointer;
}

.discogs-youtube-name {
    margin-left: 5px;
    font-size: 16pt;
}

.record-labels-subheader {
    font-size: 16pt;
    margin-bottom: 10px;
}
                         
.record-labels {
    margin-top: 10px;
}

.back-button {
    margin-top: auto;
    margin-bottom: auto;
    font-size: 12pt;
    border-style: solid;
    border-width: 1px;
    padding: 10px;
}

.back-button:hover {
    background-color: #2d3033;
}


.audio-input-chooser {
    margin-left: 10px;
    float: right
}

.audio-input-chooser-dropdown {
    width: 150px;
    height: 40px;
    font-size: 8pt;
    border-color: #403f3f;
}

.player-button {
    background-color: #023ee9 !important;
    color: white !important;
    border-width: 1px;
}

.tag-sounds-button {
    margin-bottom: 20px;
}

.big-button:hover {
    opacity: 0.8;
}

.big-button {
    font-size: 12pt;
    padding: 5px;
    color: white;
    border-style: solid;
    border-color: #42f4f1;
    border-width: 1px;
}

.tag-samples-button {
    background-color: #056460 !important;
}


.internet-navigator {
    width: 600px;
}

.internet-navigator.show-sounds {
    width: 500px;
}

.tag-discover-input {
    width: 350px;
    height: 40px;
    background-color: black !important;
}

.discover-full-view {
    height: 100%;
    display: flex;
    flex-direction: column;
    flex: 1;

    .btn {
        background-color: #464d61 !important;
        color: white !important;
    }

    .cover-art {
        height: 450px;
        width: 450px;
    }
}

.discover-view {
    padding: 20px;
    width: 90%;
    margin-left: auto;
    margin-right: auto;
    height: 100%;
    padding-bottom: 200px;
    display: flex;
    flex-direction: row;
    background-color: $blackGray;
    flex: 1;
}

.universal-sequences-logo.DISCOVER {
    background-color: #464d61 !important;
}

.discover-full-view {
    .navigator {
        background-color: $blackGray !important;
    }
}

.description-pane {
    #search-input {
        color: white !important;
    }

    .discogs-thumbnail {
        height: 80px;
        width: 80px;
    }

    .record-info {
        font-size: 10pt;
        margin-left: 10px;

        .year, .title {
            padding: 3px;
            background-color: #464d61;
        }
    }
    
    .option-tabs {
        margin-left: 0px;
    .option-tab {
        padding: 5px;
        margin-right: 20px;
        margin-left: 0px !important;
        font-size: 12pt;
        cursor: pointer;
        padding-bottom: 5px;
        border-style: solid;
        border-width: 1px;
        border-left-style: none;
        border-top-style: none;
    }
    
    .option-tab.selected {
        border-bottom-style: solid;
        border-bottom-width : 1px;
        border-left-style: solid;
        border-top-style: solid;
        border-right-color: transparent;
        border-bottom-color: transparent;
    }
    
    .option-tab:hover {
        border-color: $blueGray;
        border-top-style: solid;
        border-left-style: solid;
    }
    }
}
