.piano-roll-wrapper {
    position: relative;
    
    .parameter-automation {
        position: absolute;
        top: 28px;
        width: 100%;
        height: 100%;
        background-color: black;
        opacity: 0.8;
        z-index: 50;
    }
}


.automation-toolbar {
    display: flex;
    height: 20px;
    background-color: #1b1e3c;
    
    .dropdowns {
        display: flex;
    }
    
        .dropdowns.disabled {
            pointer-events:none;
            opacity: 0.3;
        }

        .automation-icon {
            height: 20px;
            width: 20px;
            cursor: pointer;
            margin-left: 5px;
        }

        .toolbar-resizer {
            flex: 1;
            cursor: row-resize;
        }

        .Dropdown-option {
            font-size: 10pt !important;
            padding-top: 2px !important;
            padding-bottom: 2px !important;
            padding-left: 10px !important;
        }

        .Dropdown-option.has-automations {
            color: #f3235e !important;
        }

        .Dropdown-control {
            border: none;
            height: 20px;
            line-height: 5px;
            font-size: 10pt;

            .Dropdown-arrow {
                top: 7px;
            }
        }
    }


.installation {
    display: flex;
    flex-direction: column;
    height: 100%;
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
    -khtml-user-select: none; /* Konqueror HTML */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently
                          supported by Chrome and Opera */
    
    .guilds-menu, .midi-input-dropdown-container, .lock-button, .piano-roll-button, .patterns-component {
        display: none;
    }
    
    .sequencer-dashboard {
        display: flex;
        margin: 15px;

        .dashboard-module {
            margin-right: 15px;
            padding: 5px;
            width: 150px;
            height: 150px;
            background-color: #141415;
            color: #a5a5a5;

            .module-name {
                font-size: 12pt;
            }

            .inner-module {
                font-size: 10pt;
            }
        }
    }

    .arrangement-resizing-edge {
        width: 100%;
        height: 10px;
        background-color: #5c1bc7;
        opacity: 0.5;
        cursor: row-resize;
    }

    .scroll-area {
        overflow-y: scroll;
        height: 100%;
        flex: 1;
    }

    .arrangement-bottom-container {
        z-index: 20;
        overflow-y: hidden;
        .piano-roll-container {
            padding-top: 0px;
        }
    }

    .main-area {
        background: none;
    }

    .tracks-container {
        flex: 0;
        margin-left: 15px;

        .track-right-buttons {
            margin-right: 20px;
        }
        .track-footer-container {
            display: none;
        }

        .delete-button {
        }
    }

    .bpm-component {
        opacity: 0.8;
    }


}
.sound-thumbnail {
    height: 25px;
}

.youtube-info {
    .volume-box {
        margin-top: 3px;
        margin-left: 0px;
        height: 20px;
        
        .volume-indicator {
            height: 20px;
        }
    }
}


.midi-input-dropdown-container { 
    display: none;
}
