.presets-date-and-tags {
    float: right;
    margin-right: 10px;
    display: flex;
}

.preset-thumbnail {
    margin-right: 5px;
    float: left;
    width: 40px;
    height: 34px;
    color: black;
    line-height: 8px;
    font-size: 7pt;
    padding-top: 2px;
    padding-left: 2px;
    cursor: pointer;
}

.folder-button {
    margin-right: 5px;
}

.preset-dropdown {
    height: 20px;

    .btn {
        padding-top: 0px !important;
        height: 20px;
        .medium {
            line-height: 15px;
            font-size: 15pt;
        }

    }
}

.preset-date {
    padding: 3px;
    margin-right: 10px;
    background-color: #1f1d1d;
    font-size: 7pt;
}

.preset-add-tags-button {
    padding: 3px;
    cursor: pointer
}

.preset-tags {
    display: flex;
    
    .choose-folder-button {
        font-size: 8pt;
        margin-top: 10px;
        line-height: 10px;
        margin-left: 10px;
    }
}

.user-name-and-date {
    display: flex;
}


.add-preset-tag {
    margin-left: 5px;
    color: #f92360;
    cursor: pointer;
}

.add-preset-tag.new-tag {
    margin-left: 0px;
}

.preset-search-container {
    margin: 5px;
}

.share-preset-button {
    float: left;
}

.presets-filter {
    .tag.selected {
        background-color: #3b3a61 !important;
    }
}
