.downloader {
    .title {
        font-size: 18pt;
    }
   

    .tag {
        color: #ff5656;
        margin-left: 15px;
        font-size: 10pt;
    }

    .current, .date {
        color: blue;
    }

    .divider {
        color: red;
    }

    .file-name {
        color: #bae7ff;
    }

    .action {
        color: #72ffc1;
    }

    .download-start-button {
        padding: 5px;
        background-color: red;
        color: white;
        cursor: pointer;
    }
}
