.details-heading {
    font-size: 20px;
    float: left;
}

.details-name-container {
    float: left;
    margin-left: 15px;
}

.details-back-button {
    cursor: pointer;
    clear: both;
}

.name-input {
    background-color: black;
    color: white;
    width: 300px;
    height: 35px;
    padding: 5px;
}

.amp-envelopes {
    float: left;
    width: 220px;
}

.pitch-envelopes {
    float: left;
    width: 220px;
}

.recordings-dropdown-container {
    height: 60px;
}

.recordings-dropdown-placeholder {
    font-size: 8pt !important;
}

.details-banner {
    display: flex;
    flex-direction: row;
}

.banner-back-button {
    font-size: 18pt;
    background-color: #101010;
    padding: 10px;
    cursor: pointer;
}

.banner-heading {
    font-size: 26pt;
    margin-left: 20px;
}

.sound-waveform.details-view {
    border-color: #651212;
    border-width: 1px;
}

/* Sound Tag Editor */
.keyboard-tag-pane {
    margin-top: 10px;
    float:left;
    margin-left: 20px;
}

.sound-parameters {
    margin-top: 10px;
    margin-left: 20px;
    clear: both;
}

.sound-parameters-full {
    float: left;
}

.sound-parameters-heading {
    font-size: 20px;
    margin-bottom: 20px;
}

.sound-parameter-name {
    margin-top: 10px;
    font-size: 14px;
}

.extracting-stems {
    color: red;
}

.onset-threshold-container {
    font-size: 10pt;
    float: left;
    background-color: #03151f;
    padding: 3px;
    width: 200px;
}
