.step.off.selection {
    background-color: red !important;
    opacity: 0.3 !important;
}

.step.on.selection {
    opacity: 0.3 !important;
}

.step.selected {
    background-color: #ff8100 !important;
    border-color: #83f4ff !important;
    border-width: 3px;
}

.step.off {
    background-color: rgba(0,0,0,0.4);
    step-parameter {
        color: white;
    }
}

.step.on {
    .step-parameter {
        color: black !important;
        padding-left: 6px;
        padding-right: 6px;
    }
}

.effects-parameters-button-container {
    .small-button {
        margin-left: 5px;
        background-color: black !important;
    }
}

.step-container {
    .fill {
        height: 16px;
    }
}

.fill.current-step {
    border-bottom: 2px solid red;
}

.fill.cursor {
}

.fill.cursor.on {
}

.step.current-step {
}

@keyframes blink { 50% { border-color: #c4ffec; }  }
@keyframes blink2 { 50% { border-color: #f2ffc9; }  }

.step.cursor {
    border-width: 2px;
}

.step.vi-mode.cursor {
    animation: blink .3s step-end infinite alternate; 
}

.step.cursor {
    animation: blink2 .3s step-end infinite alternate; 
}

.step.on.vi-mode.cursor {
    border-top-color: #ff549e !important;
    border-right-color: #ff549e !important;
    border-left-color: #ff549e !important;
}

.step.on.cursor {
    border-top-color: #fc54ff !important;
    border-right-color: #fc54ff !important;
    border-left-color: #fc54ff !important;
}

.step.off.cursor {
    border-color: #fc54ff !important;
}

.step.off.vi-mode.cursor {
    border-color: #ff549e !important;
}


.step {
    border-color: #537bd4;
}

.step.on {
}

.step.alternate {
    border-color: #2245a5;
}

.step-parameter {
    margin-bottom: 5px;
    color: white;
    display: block;
}

.step-position {
    font-size: 4pt;
    color: black;
    text-align: center;
}

.steps-row-bottom {
    width: 100%;
    height: 2px;

    .current-step-box {
        background-color: red;
        width: 28px;
        height: 2px;
    }
}

.steps-row.odd {
    .step {
        border-color: #2245a5;
    }

    .step.alternate {
        border-color: #537bd4;
    }
}
