.sample-packs {
}

.main-container {
    display: flex;
    flex-direction: row;
}

.sample-pack-pane {
}

.sounds-searcher-pane {
    border-style: solid;
    border-color: #110f1f;;
    border-width: thin;
    padding: 5px;
    width: 50%;
}

.sample-pack-input {
    width: 300px !important;
    margin-bottom: 5px;
    height: 40px !important;
    font-size: 20px !important;
}

.show-selector-button {
    cursor: pointer;
}

.main-buttons-container {
    margin-top: 10px;
    margin-bottom: 10px;
}

.sample-list {
    clear: both;
    margin-top: 50px;
    display: flex;
    flex-direction: column;
}

.video-thumbnail {
    width: 50px;
    height: 50px;
}

.sound-name {
    font-size: 6pt;
}

.just-sound-name {
    margin-top: 30px;
}

.sub-header {
    font-size: 20pt;
    margin-bottom: 10px;
}

.big-button {
    font-size: 20pt;
    color: #52ffdf;
    cursor: pointer;
}

.selector-button {
    margin-bottom: 10px;
}

.info-text {
    font-size: 9pt;
    color: #f92360;
    float: right;
}

.tags-label {
    margin-right: 5px;
}

.clear-tags-button {
    cursor: pointer;
    font-size: 8pt;
    margin-left: 15px;
    
}
