.sidebar-header {
    background-color: #1e1e1f;
    padding: 5px;
    display: flex;
}

.sidebar-container, .track {
    ::-webkit-scrollbar {
        opacity: 0;
        width: 8px;
        height: 8px;
    }
    
    ::-webkit-scrollbar-thumb {
        background: #2a2a2a;
        -webkit-border-radius: 1ex;
        -webkit-box-shadow: 0px 1px 2px #141414;
    }
    
    ::-webkit-scrollbar-corner {
        opacity: 0;
    }

}
.sidebar-container {
    .preset-folders {
        overflow: scroll;
        max-height: 320px;
        margin-top: 20px;
        background: none !important;
        border: solid 1px #6e050c !important;
        padding: 3px;
    }
}

#sidebar-holder {
    min-width: 360px;
}

#sidebar-holder-selector {
    min-width: 510px;
}

#sidebar-holder-collapsed {
    min-width: 50px;
}

.sounds-pane.collapsed {
    width: 40px;
    height: 1000px;
    margin-left: auto;
}

.sounds-pane {
    clear: both;
    height: 100%;
    margin-left: auto;
    margin-bottom: 10px;
    background: linear-gradient(#262930,#000 40%);
    min-height: 800px;
    width: 510px;
    border-top: 1px solid  #333639;
    border-bottom: 1px solid  #333639;
    border-left: 1px solid  #333639;
    padding-bottom: 100px;
}

.hide.sounds-pane {
    .sidebar-header {
        border-style: none;
    }

    width: 360px;
    border: none !important;
    background-color: black;
    border-radius: 0px;
    padding: 0px;

    .btn-group {
        margin-left: 25px;
        margin-top: 25px;
        margin-bottom: 20px;
    }
}


.track-quick-view {
    .mini-thumbnail {
        width: 25px;
        height: 25px;
        margin-right: 5px;
    }

    padding: 5px;
    border-style: solid;
    border-color: #6e050c;
    border-width: thin;
    width: 320px;
    margin-top: 20px;

    .parameter-options {
        display: flex;

        .parameter-option {
            padding: 3px;
            font-size: 8pt;
            margin-right: 3px;
            margin-right: 3px;
            border: 1px solid #3100a2;
            border-top-right-radius: 5px;
            cursor: pointer;
        }

        .parameter-option.selected {
            background-color: #3100a2;
        }

        .parameter-value {
            background-color: #100f29;
            flex: 1;
            padding: 3px;
            font-size: 8pt;
            border: 1px solid #3102a2;
        }
    }

    .slider-handle {
        background-image: linear-gradient(#3100a2, #5700d8) !important;
    }

    .chokes-and-matches {
        margin-top: 20px;
        display: flex;
        
        .choke {
            display: flex;
            
            .choke-label {
                margin-right: 8px;
                font-size: 9pt;
                
            }
            .tracks-dropdown-container {
                .osc-type-dropdown {
                    float: none;
                    font-size: 8pt;
                    width: 90px;
                }
            }
        }
    }
}

.keyboard.mini {
    font-size: 7pt;
    padding: 3px;
    margin-left: 0px !important;

    .key {
        margin: 2px;
        width: 30px;
    }
}


.contextual-help.empty {
    padding: 0px;
}

.pads.mini {
    padding: 5px;
    border-style: solid;
    border-color: #6e050c;
    border-width: thin;
    width: 170px;
    height: 190px;
    margin-left: 0px !important;
    margin-top: 20px;
}

.pads-row {
    clear: both;
}

.name-component {
    display: flex;
    margin-bottom: 5px;

    .name-label {
        font-size: 8pt;
        background-color: #1d1d4f;
        margin-right: auto;
    }

    .sample-arrows {
        display: flex;

        background-color: #3100a2;
        padding: 5px;
    }

    i:hover {
        border-color: blue;
    }

    i {
        cursor: pointer;
        border: solid white;
        border-width: 0 3px 3px 0;
        display: inline-block;
        padding: 5px;
        height: 15px;
    }
    
    .right {
        transform: rotate(-45deg);
        -webkit-transform: rotate(-45deg);
    }
    
    .left {
        transform: rotate(135deg);
        -webkit-transform: rotate(135deg);
    }
}

