.tags-hierarchy > .tag {
    flex-grow: 2; 
    padding: 5px;
    text-align: center;
    border-style: solid;
    border-width: 1px;
    background-color: #211f1f;
    border-color: #082435;
    cursor: pointer;
}

.tag:hover {
    background-color: #113131;
}

.tags-hierarchy {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    clear: both;
}

.parent-tag {
    cursor: pointer;
    background-color: #323638;
    border-top: 1px solid #242424;
    color: white;
    font-weight: 700;
    font-size: 18pt;
    flex-grow: 2;
    width: 100%;
    text-align: center;
}

.parent-tag:hover {
    background-color: #113131;
}

.more-button {
    border-style: solid;
    border-width: thin;
    border-color: #211b1b;
    background-color: #082435;
    flex-grow: 2;
    width: 100%;
    color: white;
    cursor: pointer;
    text-align: center;
    margin-bottom: 5px;
}

.more-button:hover {
    background-color: #0d3c58;
}
