.effect-rack {
    .horizontal {
        display: flex;
    }

    .draw-component {
        background-color: #2b2d3c;
    }

    .effect-dropdown {
        .Dropdown-control {
            width: 70px;
            height: 25px;
            padding: 4px;
            border: none;

        }
        .Dropdown-menu {
            width: 25%;
            max-height: 300px;

            .Dropdown-option {
                padding: 3px;
            }
        }
    }
}
