
.explore.hidden {
    display: none;
}

.explore {
    .navigator {
        background-color: #1c1c1c;
        width: 100%;
        height: 55px;
    }
    .navigator-button.selected {
        background-color: #040404;
    }

    .universal-sequences-logo.EXPLORE {
        background-color: #0b0935;
    }

    .show-options {
        display: flex;
        margin-top: 20px;

        .show-option {
            border: 1px solid blue;
            padding: 3px;
            margin-right: 10px;
            border-top-right-radius: 5px;
            cursor: pointer;
        }

        .show-option.show {
            background-color: blue;
        }
    }
 
}

.explore-inner {
    margin-top: 20px;
    display: flex;

    .mix-player-pane {
        margin-top: 0px;
        float: none;
        background-color: black;

        .track-user-name {
            color: black;
        }

        .track-date {
            background-color: #eaeaea;
            color: black;
        }

        .spotlight-track {
            border: none;

        }
    }

    .star {
        color: yellow;
        cursor: pointer;
    }

    .star.starred {
        display: block !important;
    }
    
    .star > span:hover:before {
        content: "\2605";
        position: absolute;
    }
    
    .star.starred > span:before {
        content: "\2605";
        position: absolute;
    }

    .tracks-filter {
        display: flex;
        width: 550px;

        .heart {
            margin-top: 5px;
            margin-left: 5px;
        }

        .user-to-filter {
            background-color: #6200ff;
            margin-left: auto;
            cursor: pointer;
            padding: 2px;
        }
    }
}

.tracks-list {
    width: 550px;
    #project-searcher {
        width: 98%;
        background-color: #151618;
        color: white;
        height: 40px;
        font-size: 13pt;
    }

    .table-header {
        display: flex;
        width: 100%;

        .table-name {
            flex: 1;
        }

        .table-collaborators {
            width: 125px;
        }
    }

    .track:hover {
        background-color: #101047;
        
        .heart {
            display: block !important;
        }

        .star {
            display: block !important;
        }
    }

    .track {
        position: relative;
        background-color: #0a0a35;
        width: 550px;
        border-style: none;
        border-bottom: 1px solid #141418;
        margin-top: 1px;
        margin-bottom: 0px;
        cursor: pointer;
            
        .track-metadata {
            display: flex;
            margin-top: 5px;

            .track-tags {
                flex: 1;
                display: flex;
                .track-tag {
                    padding: 1px;
                    background-color: #6200ff;
                    font-size: 8pt;
                    margin-right: 1px;
                }

                .add-tag-button {
                    background-color: red;
                    font-size: 9pt;
                    padding: 1px;
                    text-align: center;
                    margin-bottom: 0px;
                    border-radius: 3px;
                }
            }

           }


        .track-main-info {
            display: flex;
            
            .track-info{
                flex: 1;
                display: flex;
                height: 100%;
                
                .track-date {
                    font-size: 8pt;
                    background-color: #0e0f44;
                    width: 70px;
                    padding: 3px;
                    margin-top: 8px;
                }
            }

            .star {
                margin-left: 5px;
                margin-top: 10px;
                font-size: 9pt;
                display: none;
            }
            
            .track-name {
                padding: 3px;
                font-size: 18px;
                background-color: black;
                color: white;
                height: 100%;
                margin: 0px;
            }
            
            .track-collaborators {
                min-width: 120px;
                display: flex;
                flex-direction: column;
                
                .track-author {
                    font-size: 18px;
                    background-color: #111111;
                    padding: 3px;
                    margin-bottom: 1px;
                }

                .track-author:hover {
                    box-shadow: 0px 0px 1px 1px #5b5b5c;
                }
            }

            .heart {
                margin-left: auto;
                display: none;
                margin-top: 10px;
                margin-right: 5px;
            }
            
            .heart.favorited {
                display: block;
            }
        
        }
    }

}

.track-tagger {
    color: black;
    margin-top: 25px;

    .sequence-tag-input {
    }
}

.sequence-note-container {
    .heart {
        margin-top: 2px;
        margin-left: 2px;
    }
}

.explore-inner {
    .mixer {
        margin-top: 20px;
        margin-bottom: 20px;
        margin-left: 10px;

        .channel-strips-container {
            display: flex;

            .transpose-container {
                height: 65px;
                padding-left: 23px;

                .transpose-value {
                    fill: white;
                }
            }

            .effects-container {
                width: 60px;

                .effects-frequencies {
                    display: flex;
                    margin-top: 10px;
                    margin-bottom: 30px;

                    .frequency-option {
                        margin-right: 5px;
                        font-size: 8pt;
                        cursor: pointer;
                        padding: 3px;
                        border-radius: 3px;
                    }

                    .frequency-option.toggled {
                        background-color: red;
                    }
                }

                .effects-option {
                    padding: 2px !important;
                    margin-top: 4px;
                    font-size: 8pt;
                    border-radius: 3px;
                    background-color: #262626;
                    cursor: pointer;
                }

                .effects-option.toggled {
                    background-color: red;
                }
            }

            .channel-strip.side {
                width: 80px;
                font-size: 10pt;
                margin-right: 5px;
                
                div {
                    padding: 0px;
                }

                .low, .mid, .high {
                    padding-left: 0px !important;
                }
            }

            .channel-strip {
                min-width: 100px;
                border: solid 1px black;

                .color-container {
                    padding-left: 15px;
                    height: 80px;
                }

                .eq-container {
                    .low, .mid, .high {
                        padding-left: 23px;
                        height: 65px;
                    }

                    .knob-value {
                        fill: white;
                    }
                }

                .fader-container {
                    height: 130px;
                    padding-left: 35px;
                    padding-right: 35px;

                    .slider-vertical {
                        height: 120px !important;

                        .slider-handle {
                            border-radius: 2px;
                            width: 50px;
                            left: -9px;
                            background: #747474 !important;
                        }

                        .slider-track {
                            width: 20px;
                            background-image: linear-gradient(to bottom, #6c757a, #414141 100%);
                        }
                    }
                }

                .channel-name {
                    padding: 3px;
                    height: 40px;
                    font-size: 8pt;
                    color: black;
                    text-align: center;
                    width: 100px;
                    
                }
            }

            .channel-strip {
                .play-button {
                    float: none;
                }

                .sub-sequence-name {
                    text-align: center;
                }

                .track-patterns {
                    margin-top: 10px;
                    font-size: 8pt;

                    .pattern-button {
                        margin: 1px;
                    }
                    display: flex;
                    .sub-sequence-patterns {
                        display: flex;
                        flex-direction: column;
                    }
                }
            }
        }
    }

    .sequence-module {
        display: flex;
        flex-direction: column;
        margin-left: 10px;
        flex: 1;
        flex-wrap: wrap;

        .sub-sequence-knobs {
            margin-top: 10px;
            display: flex;
            flex-wrap: wrap;

            .sub-sequence-knob {
                margin-right: 10px;

                .sub-sequence-name {
                    text-align: center;
                }
            }
        }

        
        .sequence-name {
            width: 100%;
            color: black;
            padding: 3px;
            font-size: 8pt;
        }

        .track-mutes {
            display: flex;
            flex-wrap: wrap;

            .track-mute {
                margin-right: 4px;
                margin-top: 4px;
                width: 30px;
                height: 30px;
                cursor: pointer;
                border: 1px solid #0f5aff;

                img {
                    width: 28px;
                    height: 28px;
                }
            }
        }
    }

    .projects {
        min-width: 550px;
        overflow-y: scroll;
    }
    .sticky-explore {
        margin-left: auto;
    }
}


