.documentation {
    display: flex;
    min-height: 1000px;

    .track {
        color: white;
    }

    .doc-sidebar {
        width: 200px;
        min-height: 1000px;
        border-right: 4px solid black;

        .topic {
            padding: 5px;
            cursor: pointer;
        }

        .topic:hover {
            background-color: #1b1b21;
        }

        .topic.selected {
            background-color: #a6edd6;
            color: black;
        }
    }

    .doc-viewer {
        flex: 1;
        padding: 15px;

        p {
            margin-top: 10px;
            margin-bottom: 10px;
        }

        .sub-header {
            font-size: 16pt;
        }

        .header {
            font-size: 20pt;
            margin-bottom: 20px;
            border-bottom: 5px solid black;
            color: white;
        }

        .shortcuts {
            b {
                font-size: 14pt;
            }
        }
    }

    .track-documentation {
        display: flex;
        flex-direction: column;
    }

    .toolbar {
        padding-top: 0px !important;
    }

    b {
        color: white;
    }

    .play-container {
        display: flex;
        background-color: black;
        border: 2px solid #1e1e1e;

        .contextual-help {
            margin-left: 10px;
            height: 50px;
            width: 100%;
            flex: 1;
        }
    }

    .resolution-container {
        margin-left: auto;
        font-size: 9pt;
        margin-bottom: 20px;
    }

    .selector-container {
        margin-top: 20px;
        margin-bottom: 10px;
        border: solid 1px #072535;
        width: 500px;

        #search-input {
            background-color: black !important;
            color: white;
        }
    }

    .patterns-container {
        background-color: black;
        border: 2px solid #1d1e21;
        margin-left: 0px;
    }        

    .demo-effect-container {
        margin-top: 10px;
    }

    .presets-demo-container {
        width: 80%;
    }

    .groups-component {
        margin-top: 15px;
    }

    .sample-source-demo {
        padding-left: 10px;
        padding-right: 10px;
        padding-top: 10px;
        padding-bottom: 2px;
        background-color: #040512;
        margin-top: 10px;
        border: solid 1px #464d61;
        display: flex;
        width: 400px;
    }
}


