.palette-container {
    clear: both;
}

.palette-sample-pack {
    clear: both;
    border-style: solid;
    border-width: thin;
    border-color: #014c4b;
    padding: 5px;
}

.palette-sample-pack-name {
    font-size: 10pt;
    padding: 3px;
    background-color: #014c4b;
    margin-bottom: 5px;
}

.palette-sample-name {
    font-size: 6pt;
}

.palette-sample {
    cursor: pointer;
}

.palette-sample-thumbnail {
    width: 50px;
    height: 50px;
    margin-right: 5px;
    margin-top: 5px;
}

.palette-sample-pack-samples {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.palette-sub-header {
    cursor: pointer;
    margin-top: 10px;
    clear: both;
    font-size: 12pt;
    float: left;
    border-top-style: solid;
    border-left-style: solid;
    border-right-style: solid;

    border-top-color: #014c4b;
    border-left-color: #014c4b;
    border-right-color: #014c4b;

    padding: 5px;

    background-color: #0d1910;
}

.info-text {
    font-size: 9pt;
    color: #f92360;
    float: right;
}

.delete-button {
    float: right;
    color: red;
}

.preset-header-container {
    margin-left: 4px;
    font-size: 16pt;
}

.preset-header {
    float: left !important;
}

.save-project-preset-button {
    margin-left: 50px !important;
    float: right !important;
}
