
.arrangement-view {
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
    -khtml-user-select: none; /* Konqueror HTML */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently
                          supported by Chrome and Opera */      
 
    display: flex;
    height: 100%;
    width: 100%;
    flex: 1;

    .arrangement-bottom-container {
        width: 100%;
        z-index: 8;
        background-color: black;
        border: 1px solid #205b3d;
        overflow: scroll;

   }

    .arrangement-resizing-edge {
        width: 100%;
        height: 10px;
        cursor: row-resize;
        background-color: #17281d;
        opacity: 0.3;
    }

    .full-arrangement-container {
        display: flex;
        width: 100%;
        height: 100%;
        overflow-y: scroll;
        overflow: scroll;

        .side-panel-container {
            background-color: #0b0e0d;

            .btn-primary {
                background-color: #08350e !important;
                padding: 4px;
                margin-bottom: 18px;
            }
        }

        .arrangement-container {
            overflow-y: hidden;
            overflow-x: scroll;
        }

        .viewport-wrapper {
            position: relative;
            display: flex;
            flex-direction: column;
            height: 100%;
        }
    }

    .arrangement-rest {
        background-color: black;
        opacity: 0.6;
        flex: 1;
        //width: 30000px;
    }

    .selection-container {
        z-index: 1;
        height: 24px;

        .selection {
            height: 12px;
            border: 1px solid gray;
            background-color: black;
            z-index: 4;
            display: flex;

            .triangle-right {
                width: 0;
                height: 0;
                border-top: 5px solid transparent;
                border-left: 10px solid #686d6a;
                border-bottom: 5px solid transparent;
                cursor: w-resize;
            }

            .middle {
                flex: 1;
                cursor: col-resize;
            }

            .triangle-left {
                width: 0;
                height: 0;
                border-top: 5px solid transparent;
                border-right: 10px solid #686d6a;
                border-bottom: 5px solid transparent;
                cursor: e-resize;
            }
        }
        
        .selection.looping {
            background-color: #686d6a;
            .triangle-left {
                border-right-color: #120a0a;
            }
            .triangle-right {
                border-left-color: #120a0a;
            }
        }
    }

    .clock-ticker {
        position: absolute;
        margin-top: 2px;
        height: 100%;
        width: 2px;
        background-color: red;
        opacity: 0.4;
        z-index: 10;
    }

    .mouse-helper {
        position: absolute;
        height: 60px;
        width: 2px;
        background-color: orange;
        opacity: 0.1;
        z-index: 9;
    }

    .time-info {
        height: 25px;
        width: 140px;
    }

    .pattern-info {
        font-size: 12pt;
        line-height: 60px;
        width: 140px;
        height: 65px;
        background-color: #000c19;
        display: flex;
        flex-direction: column;
        border: 1px solid #26272d;

        .panel-rest {
            display: flex;
            flex-direction: column;
            padding-right: 10px;

            .track-controls-container {
                display: flex;
                margin-top: auto;

                .volume-box {
                    margin-top: 0px;
                    width: 60px;
                    height: 20px;
                    line-height: 20px;
                    border: none;

                    .volume-indicator {
                        height: 20px;
                        background-color: #3eff79;
                    }
                }
            }
        }

        .pattern-name {
            flex: 1;
        }
    }

    .arrangement-column {
        display: flex;
        flex-direction: column;
        width: 100%;

        .sub-sequence-container {
            display: flex;
            flex-direction: column;
        }
        
        .pattern-row.rolls-row {
            margin-bottom: 0px;
            flex-direction: row;
        }

        .pattern-row.selected {
            opacity: 1;
        }

        .pattern-row {
            display: flex;
            flex-direction: column;
            height: 65px !important;
            border-top-style: solid;
            border-top-width: 1px;
            border-top-color: #393737;
            border-bottom-style: solid;
            border-bottom-width: 1px;
            border-bottom-color: #393737;
            background-color: rgba(0, 0, 0, .2);;
            z-index: 3;
            white-space: pre; // Key line
            opacity: 0.8;

            .patterns-arrangement-container {
                display: flex;
            }

            .empty-roll-event {
                display: flex;
            }

            .roll-event {
                display: flex;
                z-index: 8;

                .pattern.last {
                    flex-basis: 0;
                }
            }
            
            .pattern.selected {
            }

            .pattern.last {
                border-right-color: red;
                border-right-style: dashed;
            }

            .small-pattern.pattern {
                height:20px;
            }

            .pattern {
                position: relative;
                flex-wrap: nowrap;
                flex-shrink: 2;
                font-size: 8pt;
                display: flex;
                flex-direction: column;
                border-style: solid;
                border-width: 1px;
                border-color: #303138;
                text-align: center;
                color: black;
                cursor: pointer;
                word-wrap: break-word;

                .pattern-top {
                    width: 100%;
                    height: 20px;
                    z-index: 8;
                    display: flex;

                    .center {
                        flex: 1;
                    }
                    .left-edge {
                        cursor: col-resize;
                        height: 100%;
                        width: 12px;
                    }
                    .right-edge {
                        cursor: col-resize;
                        height: 100%;
                        width: 12px;
                        margin-left: 0px !important;
                    }
 
                }

                .pattern-rest {
                    height: 42px;
                    display: flex;
                    opacity: 0.4;
                    background-color: rgba(13, 7, 24, 0.92);
                }

                .pattern-number {
                    flex: 1 ;
                    width: 100%;
                }
           }
        }
        
        .time-background {
            left: 100px;
            position: absolute !important;
            z-index: 0;
            opacity: 0.2;

            .bar {
                height: 50px !important;
            }
        }

        .time-row.bottom {
            .bar {
                border-top: 1px solid #363636;
            }
            
            margin-top: auto;
        }
        .time-row, .time-background {
            display: flex;
            cursor: zoom-in;
            
            .bar {
                background-color: #000d19;
                font-size: 8pt;
                height: 25px;
                line-height: 10px;
                
                display: flex;
                border-bottom: 2px solid #363636;

                
                .beat {
                    margin-top: auto;
                    padding-top: 2px;
                    padding-left: 2px;
                    flex-basis: 25%;
                    flex-grow: 0;
                    height: 750px;
                    z-index: 0;
                    border-left-style: solid;
                    border-left-width: 1px;
                    border-left-color: #122c12;
                }
                
                .first-beat {
                    flex-basis: 25%;
                    flex-grow: 0;
                    margin-top: auto;
                    z-index: 0;
                    height: 750px;
                    border-left-style: solid;
                    border-left-width: 1px;
                    border-left-color: #205b3f;
                }

                .beat.selected, .first-beat.selected {
                    background-color: orange;
                }
            }
        }
    }
    .pattern-info {
        .sub-sequence-name {
            font-size: 9pt;
            padding-left: 11px;
            line-height: 20px;
            color: white;
            background-color: #0a0f0e;
            cursor: pointer;
        }
    }

    .pattern-info.selected {

        .sub-sequence-name {
            background-color: #0ff782;
            color: black;
        }
    }

    .pattern-info.sub-sequence {
        padding: 0px;
        line-height: 30px;
        
        .option-and-label {
            display: flex;
            flex-direction: column;
       }
        
        .automation-type-dropdown {
            line-height: 2px;
            width: 85px;

            .automation-type-control-dropdown {
                font-size: 9pt;
                border-style: none !important;
                border-radius: 0px;
                height: 20px;
                line-height: 3px;
            }

            .Dropdown-arrow {
                top: 6px !important;
            }

            .Dropdown-option {
                height: 30px;
                line-height: 10px;
                font-size: 9pt;
            }
        }
    }

    .pattern-row.automation {
        position: relative;
    }

    .absolute-selection {
        position: absolute;
        height: 0px;
        z-index: 0;
        top: 38px;
    }

    .absolute-selection.looping {
        height: 600px;
        background-color: #025669;
        opacity: 0.2;
        border: 1px solid red;
    }

    .row-selection {
        position: absolute;
        height: 65px;
        background-color: red;
        z-index: 2;
        opacity: 0.5;
    }

    .linear-automation-container {
        z-index: 10;
        position: absolute;
        top: 20px;
    }

    .side-browser-container {
        min-width: 300px;
        background: linear-gradient(#1b201c, #000 40%);
        overflow-y: scroll;

        .options {
            display: flex;

            .option {
                padding: 3px;
                font-size: 8pt;
                margin-right: 5px;
                cursor: pointer;
                border-right: 1px solid white;
                border-bottom: 1px solid white;
            }

            .option.selected {
                border-right: none;
                border-bottom: none;
                border-left: 1px solid white;
                border-top: 1px solid white;
            }

        }

        .xanadu-folders {
        }

        .browser-search {
            color: white;
            background-color: black;
            width: 100%;
        }

        .preset-folders {
            background-color: #000000;
            clear: both;
            border: solid 2px #163521;

            .folders-container .folder-name.selected {
                background-color: #133a23 !important;
            }
        }
    }

    .midi-recording-button {
        margin-left: 5px;
        height: 12px;

        .plain-dot {
            margin-bottom: 12px;
        }
    }

    .mini-piano-roll-wrapper {
        display: flex;
        flex-direction: column;
        width: 100%;
        position: absolute;
        height: 65px;
        
        .mini-piano-roll-header {
            height: 16px;
            width: 100%;
            z-index: 50;
            display: flex;

            .middle {
                flex: 1;
            }
        }

        .mini-piano-roll.steps-map {
            border: none;
            width: 100%;
            height: 65px;
            position: relative;
        }
    }

    .piano-roll-container {
        padding-top: 0px;

        .piano-roll-wrapper {
            border: none;


            .time-grid-step {
                border-color: #151b28;
            }

            .time-grid-step.bar {
                border-left: 2px solid #141341;
            }

            .grid-steps-container {
                border-bottom: 1px solid #06090e;
            }

            .time-grid-step.current-step {
                border-left-color: #b41919;
            }

            .grid-step {
                z-index: 100;
            }
        }
    }
}
