.sample-packs-list {
    clear: both;
}


.sample-pack-item.odd {
    background-color: #070c13;
}

.sample-pack-item {
    clear: both;
    border-style: solid;
    border-width: thin;
    border-color: #082435;
    padding: 2px;
    height: 40px;
    background-color: #0a121c;
}

.sample-pack-edits {
    padding: 3px;
    background-color: #f92360;
    margin-left: 10px;
    font-size: 8pt;
    margin-right: 10px;
    cursor: pointer;
    float: right;
}

.sample-pack-date {
    padding: 3px;
    background-color: #1f1d1d;
    width: 110px;
    margin-left: 10px;
    font-size: 8pt;
    margin-right: 10px;
    float: right;
    text-align: center;
}


.sample-pack-name {
    padding-right: 5px;
    font-size: 10pt;
    padding-left: 5px;
    cursor: pointer;
    background-color: #082435;
}

.sample-pack-user-name {
    font-size: 6pt;
    padding: 3px;
    background-color: #0b1419;
    cursor: pointer;
}

.sample-pack-user-name:hover {
    background-color: #143c52;
}

.delete-button {
    color: #f92360;
    cursor: pointer;
    float: right;
}

.filtering-sequences-info {
    clear: both;
    padding: 3px;
    cursor: pointer;
    background-color: #082435;
    color: red;
}

.sequence-name-and-user {
    float: left;
}
