.landing-page {
    display: flex;
    flex-direction: column;
    height: 100%;
    background-color: black;
    color: #b4b4b4;

    .navigator {
        background-color: #0b0a0e;
        border-bottom: solid 4px #141313;

        .universal-sequences-logo {
            background-color: #a6edd6;
            color: #849eab;
        }

        .navigator-button:hover {
            background-color: #12131a;
        }
    }

    .content {
        clear: both;
        height: 100%;
        width: 85%;
        margin-left: auto;
        margin-right: auto;
        background-color: #0e0d12;
        flex: 1;

        .landing-navigator {
            padding: 15px;
            display: flex;
            border-bottom: 10px solid black;

            .option:hover {
                background-color: #1b1b21;
            }

            .option {
                cursor: pointer;
                margin-right: 20px;
                font-size: 14pt;
            }

            .option.selected {
                border-bottom: solid 2px #a6edd6;
            }
        }

        .mission {
            font-size: 16pt;
        }

        .collective, .decentralized {
            font-size: 15pt;
        }

        .mission, .collective, .decentralized {
            text-align: center;
            padding: 15px;
            margin-top: 15px;
            margin-bottom: 15px;
        }

        .horizontal-divider {
            width: 100%;
            height: 10px;
            background-color: black;
        }

        .try-it-now {
            cursor: pointer;
            margin-top: 15px;
            margin-bottom: 15px;
            background-color: #c8173f;
            padding: 10px;
            text-align: center;
            font-size: 18pt;
            margin-right: auto;
            margin-left: auto;
            width: 150px;
        }

        .capabilities {
            margin-top: 15px;
            display: flex;
            width: 80%;
            margin-left: auto;
            margin-right: auto;

            .box {
                background-color: #171821;
                margin-left: auto;
                margin-right: auto;
                font-size: 14pt;
                width: 250px;

                .info {
                    padding: 15px;
                }

                .bottom.info {
                    border-top: 4px solid #0f0f0f;
                }
            }
        }
    }

    .getting-started {
        display: flex;
        flex-direction: column;
        margin: 15px;

        .main-text {
            font-size: 16pt;
            margin-bottom: 20px;
        }
        
        .onboarding-step.showing-how-to {
            width: 600px;
        }

        .onboarding-step:hover {
            background-color: #191b2d
        }
        
        .onboarding-step {
            border: solid 4px #17151d;
            display: flex;
            margin: 5px;
            font-size: 13pt;
            width: 500px;
            background-color: #171821;
            cursor: pointer;

            .step-checkmark {
                width: 60px;
                height: 60px;
                background-color: #5ec3ac;
                border: 4px solid #111e1b;
            }

            .step-info {
                padding: 15px;
            }

            .how-to {
                font-size: 12pt;

                .how-to-step {
                    margin: 10px;
                }
            }
        }

        min-height: 1000px;
    }

    .user-name-input {
        background-color: black;
        color: white;
    }

    .checkmark {
        display: inline-block;
        width: 40px;
        height: 50px;
        transform: rotate(45deg);
    }
    
    .checkmark_stem {
        position: absolute;
        width: 6px;
        height: 20px;
        background-color: #171821;;
        left: 19px;
        top: 8px;
    }
    
    .checkmark_kick {
        position: absolute;
        width: 6px;
        height: 6px;
        background-color: #171821;;
        left: 14px;
        top: 22px;
    }
}
