.dashboard-module {
    .rules-module {
        margin-top: 10px;

        .rule {
            margin-top: 5px;
        }
    }

    .room-module {
        margin-top: 15px;

        .people-online {
            font-size: 24pt;
            text-align: center;
            line-height: 30px;
        }

        .people-text {
            margin-top: 5px;
            font-size: 12pt;
            text-align: center;
        }
    }

    .sequencer-module {
        .steps-visualizer {
            margin-left: 5px;
            .step {
                width: 12px;
                height: 8px;
                margin-right: 2px;
                margin-left: 2px;
            }
        }

        .mix-buttons {
            margin-top: 30px;
            border: none;

            .recording-dot, .not-recording-dot {
                margin-top: 4px;
            }

            .loop-icon {
                display: none;
            }
        }
        
    }

    .actions {
        margin-top: 15px;

        .actions-remaining {
            font-size: 24pt;
            text-align: center;
            line-height: 30px;
        }
        .actions-text {
            margint-top: 5px;
            font-size: 12pt;
            text-align: center;
        }

        .submit-button {
            cursor: pointer;
            margin-top: 30px;
            background-color: red;
            color: white;
            text-align: center;
        }

        .submit-button.disabled {
            opacity: 0.1;
            pointer-events: none;
        }
    }

    .mixer-module {
        margin-top: 25px;
        display: flex;

        .track-mixer-volume.selected {
            border: 1px solid #2f3b75;
            .volume-indicator {
                width: 13px;
            }
        }

        .track-mixer-volume {
            margin-right: 5px;
            width: 15px;
            height: 80px;
            background-color: black;
            position: relative;

            .track-volume-name {
                text-align: center;
                line-height: 16px;
                text-orientation: sideways;
                writing-mode: vertical-lr;
                text-overflow: ellipsis;
                font-size: 9pt;
                white-space: nowrap;
            }

            .volume-indicator {
                opacity: 0.7;
                position: absolute;
                width: 15px;
                background-color: #21299e;
            }
        }
    }
}
